/* Global body styles */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 
    sans-serif;
  -webkit-font-smoothing: antialiased; /* For smoother text rendering */
  -moz-osx-font-smoothing: grayscale; /* For smoother text rendering on MacOS */
  color: #333; /* Darker text color for better readability */
  background-color: #f4f4f4; /* Light background color for a clean look */
}

/* Code styling */
code {
  font-family: 'Source Code Pro', Menlo, Monaco, Consolas, 'Courier New', monospace;
  background-color: #f0f0f0; /* Light grey background for code blocks */
  border-radius: 4px; /* Rounded corners for a softer look */
  padding: 2px 4px; /* Padding for better readability */
  color: #d63384; /* A color that stands out and is pleasant to the eye */
  font-size: 0.9em; /* Slightly smaller font size for code */
}

/* Code block styling (if applicable) */
pre {
  background-color: #272822; /* Dark background for code blocks */
  color: #f8f8f2; /* Light text color for code blocks */
  padding: 10px; /* Padding for better readability */
  border-radius: 4px; /* Rounded corners for a softer look */
  overflow-x: auto; /* Horizontal scrolling for long lines */
}
